.stickybar {
	position: sticky;
	top: 0;
	background-color: $light-semi-trans;
	backdrop-filter: blur(2px);
	z-index: $zindex-stickybar;
	font-size: $font-size-lg;
	line-height: $line-height-lg;
}

.stickybar-light {
	background-color: rgba($light,0.9);
}

.stickybar-primary,
.stickybar-dark {
	color: $white;
}

.stickybar-primary {
	background-color: rgba($primary,0.9);
}

.stickybar-dark {
	background-color: $dark-semi-trans;
}

.stickybar ~ [id],
.stickybar ~ * [id] {
    scroll-margin-top: 6rem; 

    @include media-breakpoint-up(lg) {
        scroll-margin-top: 4rem; 
    }
}